import { writable } from "svelte/store";
export const LocationHash = writable(window.location.hash);
export const ProjectIndex = writable(0);
export const GalleryDirection = writable(0);
export const HTMLCollectionIndexOf = (collection, item) => {
    for (let i = 0; i < collection.length; i++) {
        if (collection[i] == item)
            return i;
    }
    return -1;
};
export const HTMLCollectionIndexById = (collection, id) => {
    for (let i = 0; i < collection.length; i++) {
        if (collection[i].id == id)
            return i;
    }
    return -1;
};
export const sleep = (ms) => {
    return new Promise((resolve) => setTimeout(resolve, ms));
};
